.timeline-item-container {
  span {
    color: var(--vegas-gold);
    font-weight: var(--fw-400);
    line-height: 1.6;
  }
  .timeline-item-title {
    font-size: var(--fs-6);
    line-height: 1.3;
    margin-bottom: 7px;
  }

  .timeline-text {
    color: var(--light-gray);
    font-weight: var(--fw-300);
    line-height: 1.6;
  }
}

@media (min-width: 1250px) {
  .timeline-item-container {
    .timeline-text {
      max-width: 700px;
    }
  }
}
