.ui-icon-box-container {
  position: relative;
  background: var(--border-gradient-onyx);
  width: 30px;
  height: 30px;
  border-radius: 8px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 16px;
  color: var(--orange-yellow-crayola);
  box-shadow: var(--shadow-1);
  z-index: 1;

  &::before {
    content: "";
    position: absolute;
    inset: 1px;
    background: var(--eerie-black-1);
    border-radius: inherit;
    z-index: -1;
  }

  .ui-icon {
    --ionicon-stroke-width: 35px;
  }
}

@media (min-width: 580px) {
  .ui-icon-box-container {
    width: 48px;
    height: 48px;
    border-radius: 12px;
    font-size: 18px;
  }
}
