.soft-skill-item-container {
  margin-bottom: 25px;

  .ui-content-card-container {
    padding-top: 45px;

    .soft-skills-avatar-box {
      position: absolute;
      top: 0;
      left: 0;
      transform: translate(15px, -25px);
      background: var(--bg-gradient-onyx);
      border-radius: 14px;
      box-shadow: var(--shadow-1);
    }

    .soft-skills-item-title {
      margin-bottom: 7px;
    }

    .soft-skills-text {
      color: var(--light-gray);
      font-size: var(--fs-6);
      font-weight: var(--fw-300);
      line-height: 1.6;
      line-clamp: 4;
      overflow: hidden;
    }

    .loader {
      width: 60px;
      height: 60px;
    }
  }
}

@media (min-width: 580px) {
  .soft-skills-avatar-box {
    transform: translate(30px, -30px);
    border-radius: 20px;
  }

  .soft-skills-item-title {
    margin-bottom: 10px;
    margin-left: 95px;
  }

  .soft-skills-text {
    line-clamp: 2;
    -webkit-line-clamp: 2;
  }

  .loader {
    width: 80px;
    height: 80px;
  }
}
