.ui-category-filter-container {
    .filter-list {
        display: none;
    }

    .filter-select-box {
        position: relative;
        margin-bottom: 25px;

        .filter-select {
            background: var(--eerie-black-2);
            color: var(--light-gray);
            display: flex;
            justify-content: space-between;
            align-items: center;
            width: 100%;
            padding: 12px 16px;
            border: 1px solid var(--jet);
            border-radius: 14px;
            font-size: var(--fs-6);
            font-weight: var(--fw-300);
        }

        .filter-select.active .select-icon {
            transform: rotate(0.5turn);
        }

        .select-list {
            background: var(--eerie-black-2);
            position: absolute;
            top: calc(100% + 6px);
            width: 100%;
            padding: 6px;
            border: 1px solid var(--jet);
            border-radius: 14px;
            z-index: 2;
            opacity: 0;
            visibility: hidden;
            pointer-events: none;
            transition: 0.15s ease-in-out;

            .select-item {
                button {
                    background: var(--eerie-black-2);
                    color: var(--light-gray);
                    font-size: var(--fs-6);
                    font-weight: var(--fw-300);
                    text-transform: capitalize;
                    width: 100%;
                    padding: 8px 10px;
                    border-radius: 8px;

                    &.active {
                        color: var(--orange-yellow-crayola);
                        background: var(--jet);
                    }

                    &:hover {
                        --eerie-black-2: hsl(240, 2%, 20%);
                    }
                }
            }
        }

        .filter-select.active+.select-list {
            opacity: 1;
            visibility: visible;
            pointer-events: all;
        }
    }
}


@media (min-width: 768px) {
    .ui-category-filter-container {

        .filter-select-box {
            display: none;
        }

        .filter-list {
            display: flex;
            justify-content: flex-start;
            align-items: center;
            gap: 25px;
            padding-left: 5px;
            margin-bottom: 30px;

            .filter-item {
                button {
                    color: var(--light-gray);
                    font-size: var(--fs-5);
                    transition: var(--transition-1);

                    &:hover {
                        color: var(--light-gray-70);
                    }

                    &.active {
                        color: var(--orange-yellow-crayola);
                    }
                }
            }
        }
    }
}